@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

/* Arigami Digital Color Palette - inspired by BigFish guidelines */
:root {
  /* Primary Brand Colors - Blues from logo */
  --color-primary-blue: #3b82f6;
  --color-primary-blue-50: #eff6ff;
  --color-primary-blue-100: #dbeafe;
  --color-primary-blue-200: #bfdbfe;
  --color-primary-blue-300: #93c5fd;
  --color-primary-blue-400: #60a5fa;
  --color-primary-blue-500: #3b82f6;
  --color-primary-blue-600: #2563eb;
  --color-primary-blue-700: #1d4ed8;
  --color-primary-blue-800: #1e40af;
  --color-primary-blue-900: #1e3a8a;

  /* Secondary Colors - following BigFish vibrant system */
  --color-light-blue: #b7fbff;
  --color-mid-blue: #88a2ff;
  --color-dark-blue: #1f47e6;
  
  --color-light-pink: #ffd1e7;
  --color-mid-pink: #ff84e4;
  
  --color-mid-purple: #d987ff;
  --color-light-purple: #f3e8ff;
  
  --color-mid-yellow: #ffe03d;
  --color-light-yellow: #f6fd87;
  
  --color-mid-orange: #ff965a;
  --color-dark-orange: #d1903a;
  
  --color-mid-green: #78d692;
  --color-dark-green: #a1a500;
  
  /* Base Colors */
  --color-black: #151515;
  --color-white: #ffffff;
  --color-grey: #b6cbcb;
  --color-mid-grey: #aaaaaa;
  --color-dark-grey: #717171;

  /* Semantic Colors */
  --color-success: var(--color-mid-green);
  --color-warning: var(--color-mid-yellow);
  --color-error: #ef4444;
  --color-info: var(--color-primary-blue);
}

@layer base {
  :root {
        --background: 0 0% 100%;
        --foreground: 0 0% 3.9%;
        --card: 0 0% 100%;
        --card-foreground: 0 0% 3.9%;
        --popover: 0 0% 100%;
        --popover-foreground: 0 0% 3.9%;
        --primary: 0 0% 9%;
        --primary-foreground: 0 0% 98%;
        --secondary: 0 0% 96.1%;
        --secondary-foreground: 0 0% 9%;
        --muted: 0 0% 96.1%;
        --muted-foreground: 0 0% 45.1%;
        --accent: 0 0% 96.1%;
        --accent-foreground: 0 0% 9%;
        --destructive: 0 84.2% 60.2%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 89.8%;
        --input: 0 0% 89.8%;
        --ring: 0 0% 3.9%;
        --chart-1: 12 76% 61%;
        --chart-2: 173 58% 39%;
        --chart-3: 197 37% 24%;
        --chart-4: 43 74% 66%;
        --chart-5: 27 87% 67%;
        --radius: 0.5rem;
    }
  .dark {
        --background: 0 0% 3.9%;
        --foreground: 0 0% 98%;
        --card: 0 0% 3.9%;
        --card-foreground: 0 0% 98%;
        --popover: 0 0% 3.9%;
        --popover-foreground: 0 0% 98%;
        --primary: 0 0% 98%;
        --primary-foreground: 0 0% 9%;
        --secondary: 0 0% 14.9%;
        --secondary-foreground: 0 0% 98%;
        --muted: 0 0% 14.9%;
        --muted-foreground: 0 0% 63.9%;
        --accent: 0 0% 14.9%;
        --accent-foreground: 0 0% 98%;
        --destructive: 0 62.8% 30.6%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 14.9%;
        --input: 0 0% 14.9%;
        --ring: 0 0% 83.1%;
        --chart-1: 220 70% 50%;
        --chart-2: 160 60% 45%;
        --chart-3: 30 80% 55%;
        --chart-4: 280 65% 60%;
        --chart-5: 340 75% 55%;
    }
}

@layer base {
  * {
    @apply border-border;
    }
  body {
    @apply bg-background text-foreground;
    }
}
